const clearFieldCustomer = document.querySelectorAll('.js-clearFieldCustomer')

const clearInput = () => {
  clearFieldCustomer.forEach((clear) => {
    clear.addEventListener('click', () => {
      console.log(clear)
      const customer = clear.parentNode.querySelector('input')
      console.log(customer)
      customer.value = ''
      return false
    })
  })
}

export { clearInput }
